import './ProjectCard.css'
import me from '../res/profile/me.png'
import React, { useState } from 'react';
import {pantheonBaseUrl} from "../lib/pantheon";
import TagElement from "./TagElement";

function CharacterCard() {
    return <div className="project-card wiggle">
        <div className="inner">
            <div className="covers">
                <img className="cover cover-back"/>
                <img className="cover" src={me}/>
            </div>
            <div className="infos">
                <p className="project-title">Grgory DAVID</p>
                <div className="tags-box">
                    <TagElement name="Dveloppeur"/>
                    <TagElement name="tudiant"/>
                </div>
                <p>Un passionn du dveloppement cherchant  tre dveloppeur backend. Actuellement tudiant  Bordeaux Ynov Campus pour une dure de 5 ans (mastre).</p>
            </div>
        </div>
    </div>
}

export default CharacterCard;